import React, { Component } from 'react';

export const TextCard = (props) => {
  return (
    <div className={"TextCard " + props.type}>
      <div className="Name">
        <b>{props.name}</b>
      </div>
      <div className="Content">
        <ul>
          {props.content.map((text) => {
            return (<li>{text}</li>)
          })}
        </ul>
      </div>
    </div>
  );
}

export const TextCardList = (props) => {
  return (
    <div className="TextCardList">
      {props.cards.reverse().map((card) => <TextCard {...card} />)}
    </div>
  );
}

export class ChatForm extends Component {
  state = {
    text: ""
  }

  handleSubmit = (event) => {
    event.preventDefault();
    const http = new XMLHttpRequest();
    const url = "http://127.1.0.0";   // TODO: Make sure that this is right!

    let data = {
      "name": "Boberto Sun",
      "text": "Yippie Ki Yay!"
    }

    http.onload = () => {
      console.log(http.response);
    }
    http.open("GET", url);
    http.send(JSON.stringify(data));
  }

  handleChange = (event) => {
    this.setState({ text: event.target.value })
  }

  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <input
          type="text"
          value={this.state.text}
          onChange={this.handleChange}
          required
        />
        <button type="submit">Send</button>
      </form>
    );
  }
}

export class ChatBox extends Component {
  state = {
    cards: [
      {
        name: "Boberto",
        type: "Client",
        content: [
          "1 banana,",
          "2 banana,",
          "3 banana!"
        ]
      },
      {
        name: "Roberto",
        type: "Server",
        content: [
          "I really like bananas."
        ]
      }
    ]
  }

  render() {
    return (
      <div className="ChatBox">
        <TextCardList cards={this.state.cards}/>
        <ChatForm />
      </div>
    );
  }
}
